import {up, down} from './action.counting';
import {useDispatch,useSelector} from 'react-redux';

const Counter=()=>{const d=useDispatch(); const st=useSelector(s=>s.counter);return(<div><input type="text" disabled={true} readonly={true} value={st} /> <button onClick={()=>d(up())}>Count up by 22</button><button onClick={()=>d(down())}>Count down by 33</button></div>);};

export default Counter;